<template>
    <div class="admin-list">
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>员工管理</el-breadcrumb-item>
            <el-breadcrumb-item>管理员列表</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="search-people">
            <el-input
                placeholder="请输入内容"
                prefix-icon="el-icon-search"
                size="small"
                v-model="keyword">
            </el-input>
        </div>
        <div class="tile clearfix">
            <span class="ti bold">管理员列表</span>
            <el-button class="fr" type="primary" style="margin-top:4px" @click="dialog = true">添加子管理员</el-button>
        </div>
        <el-table
                :data="tableData"
                stripe
                height="620px"
        >
            <el-table-column
                prop="checked"
                width="50"
            >
                <template slot-scope="scope">
                    <el-checkbox v-model="scope.row.checked"></el-checkbox>
                </template>
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="员工姓名"
                    width="100"
            >
            </el-table-column>
            <el-table-column
                    prop="permission"
                    label="权限">
            </el-table-column>
            <el-table-column
                    prop="sex"
                    label="性别"
            >
            </el-table-column>
            <el-table-column
                    prop="birth"
                    label="生日"
            >
            </el-table-column>
            <el-table-column
                    prop="phone"
                    label="手机"
            >
            </el-table-column>
            <el-table-column
                    prop="control"
                    label="操作">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleClick(scope.row.name)">移除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="control" style="padding-left: 20px">
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll">全选</el-checkbox>
        </div>
        <el-dialog
            title="添加子管理员"
            :visible.sync="dialog"
            :before-close="handleClose"
            width="700px"
        >
            <div class="slot ac">
                <el-input placeholder="请输入员工电话号码" v-model="search">
                    <el-button slot="append"  type="primary">搜索</el-button>
                </el-input>
                <el-table
                    :data="searchResult"
                    height="130px"
                    v-if="searchResult.length"
                >
                    <el-table-column
                        prop="name"
                        label="姓名"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="sex"
                            label="性别"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="birth"
                            label="生日"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="phone"
                            label="手机"
                    >
                    </el-table-column>
                    <el-table-column
                            prop="name"
                            label="操作"
                    >
                        <el-button slot-scope="scope" size="small" type="primary" @click="setAdmin(scope.row.name)">设置为子管理</el-button>
                    </el-table-column>
                </el-table>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    export default {
        name: 'admin-list',
        metaInfo: {
            title: '管理员列表',
            meta: [
                {name: 'keywords', content: '福分通'},
                {name: 'description', content: '专注企业员工福利'}
            ]
        },
        data() {
            return {
                keyword:'',
                search:'',
                dialog:false,
                tableData:[
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    }
                ],
                searchResult:[
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    }
                ]
            }
        },
        computed:{
            table(){
                let data = [];
                for (let i =0;i<20;i++){
                    data.push(this.tableData)
                };
                return data
            },
            isIndeterminate(){
                let data = [];
                this.tableData.forEach(item=>{
                    if (item.checked){
                        data.push(item.checked)
                    }
                });
                if (data.length===0||data.length===this.tableData.length){
                    return false
                }else{
                    return true
                }
            },
            checkAll:{
                get(){
                    let data = [];
                    this.tableData.forEach(item=>{
                        if (item.checked){
                            data.push(item.checked)
                        }
                    });
                    if (data.length===this.tableData.length){
                        return true
                    }else{
                        return false
                    }
                },
                set(){
                    if (this.checkAll){
                        this.tableData.forEach((item,index)=>{
                            item.checked = false
                        })
                    }else{
                        this.tableData.forEach((item,index)=>{
                            item.checked = true
                        })
                    }
                }
            }
        },
        mounted(){
            this.$store.dispatch('layout/SET_ASIDE','fault')
        },
        methods:{
            handleClick(val){
                this.$confirm(`确定取消${val}的管理员资格？`)
                    .then(_ => {
                        console.log('取消了')
                        done();
                    }).catch(_ => {});
            },
            handleClose(done) {
                this.$confirm('还未进行操作，确定关闭？')
                    .then(_ => {
                        done();
                    }).catch(_ => {});
            },
            setAdmin(val){
                this.$confirm(`确定要将${val}设置为子管理员吗？`)
                    .then(_ => {
                        this.dialog = false
                        done();
                    }).catch(_ => {});
            }
        },
        beforeDestroy(){
            this.$store.dispatch('layout/SET_ASIDE','default')
        }
    }
</script>

<style lang="stylus">
.admin-list
    position relative
    .search-people
        width 200px
        position absolute
        top -8px
        right 0
    .el-breadcrumb
        margin-bottom 20px
    .tile
        height 50px
        line-height 50px
        border-bottom #eaeaea 1px solid
        border-top #eaeaea 1px solid
        margin-bottom 10px
        span.ti
            color #0090ff
            font-size 18px
    .control
        position absolute
        bottom 20px
        height 40px
        width 910px
        padding 0 10px
    thead th
        padding 6px 0
        text-align center
        color white
        background-color #243444
        &.gutter
            background-color white
    tbody tr,tbody td
        padding 5px 0
    div.cell
        text-align center
    .el-table--striped .el-table__body tr.el-table__row--striped td
        background-color #d8ebfb
    .el-pagination
        text-align right
    .slot
        width 100%
        .el-input
            width 400px
            margin 0 auto 40px
</style>
